<template>
  <div>
    <div class="row">
      <div class="col-lg-1">
        <el-button type="success" id="addTimes" @click="isAddLog = true">添加</el-button>
      </div>
    </div>
    <el-table :data="userImages" style="width: 100%" stripe height="520">
      <el-table-column prop="image" label="图片" width="180">
        <template slot-scope="scope">
          <el-image style="width: 100px; height: 100px" :src="scope.row.path"></el-image>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="图片名" width="600"></el-table-column>
      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteTimes(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加对话框 -->
    <el-dialog
      title="添加图片"
      :visible.sync="isAddLog"
      width="80%"
      :before-close="CloseAddLog"
      :show-close="false"
    >
      <form enctype="multipart/form-data" id="formAdd">
        <el-alert title="提示:以home开头为滚动图片,以mainBg开头为背景图片,默认为形象墙" type="warning"></el-alert>
        <div class="row">
          <div class="col-lg-12">
            <el-input type="text" placeholder="文件名(一定加上后缀名)" name="fileName" v-model="imageName" />
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <input type="file" id="file_image" name="fileImage" />
          </div>
        </div>
      </form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onAddLog(0)">取 消</el-button>
        <el-button type="primary" @click="onAddLog(1)">
          上传
          <i class="el-icon-upload el-icon--right"></i>
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getDate();
  },
  data() {
    return {
      userImages: [],
      isAddLog: false,
      imageUrl: "",
      imageName: ""
    };
  },
  methods: {
    getDate() {
      //获取数据
      //获取所有图像
      const resUserImg = this.$http.post("getUserImg");
      resUserImg.then(r => {
        const images = r.data.data;
        //分类存放
        for (let i = 0; i < images.length; i++) {
          this.userImages.push({
            name: images[i],
            path: this.$Http.httpImage + images[i]
          });
        }
      });
    },
    CloseAddLog() {
      //关闭
      this.imageName = "";
      $("#file_image").val("");
    },
    onAddLog(is) {
      //添加
      if (is == 0) {
        this.isAddLog = false;
      } else {
        //确定:使用原理的jqury方法
        let name = this.imageName;
        let file = $("#file_image");
        if (name == "" || file.val() == "") {
          this.$message.warning("请填充完整!");
        } else {
          //准备完毕
          var formData = new FormData(document.getElementById("formAdd")); //获取form里的数据
          const addImg = this.$http.post("addUserImg", formData);
          const loading = this.$animationLoad();
          addImg.then(r => {
            loading.close();
            this.isAddLog = false;
            this.$message.success(r.data.msg);
            this.$router.go(0);
          });
        }
      }
    },
    deleteTimes(data) {
      //删除图片
      if (confirm("确定要删除" + data.name + "图片")) {
        //点击确定
        const deletImage = this.$http.post("setUserImg", {
          imgName: data.name
        });
        const loading = this.$animationLoad();
        deletImage.then(r => {
          loading.close();
          this.$message.success(r.data.msg);
          this.$router.go(0);
        });
      }
    }
  }
};
</script>
<style scoped lang="less">
#formAdd {
  div {
    margin-top: 10px;
  }
}
</style>