<template>
  <div>
    <div class="row">
      <div class="col-lg-1">
        <el-button type="success" id="addTimes" @click="isAddLog = true">添加</el-button>
      </div>
    </div>

    <el-table :data="userTimes" style="width: 100%" stripe height="520">
      <el-table-column prop="time" label="时间" width="180"></el-table-column>
      <el-table-column prop="text" label="内容" width="600"></el-table-column>
      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteTimes(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加对话框 -->
    <el-dialog
      title="添加事情线"
      :visible.sync="isAddLog"
      width="80%"
      :before-close="CloseAddLog"
      :show-close="false"
    >
      <!-- 选择时间:
    type:类型
    editable：是否可以输入
    format：显示格式
    value-format:输出格式
      -->
      <el-date-picker
        id="dateAddLog"
        v-model="chTime"
        type="datetime"
        placeholder="选择日期时间"
        :editable="false"
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy年MM月dd"
      ></el-date-picker>
      <el-input type="textarea" :rows="5" placeholder="请输入内容" v-model="dateText"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onAddLog(0)">取 消</el-button>
        <el-button type="primary" @click="onAddLog(1)">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getData();
  },
  data() {
    return { userTimes: [], isAddLog: false, chTime: "", dateText: "" };
  },
  methods: {
    getData() {
      //获取时间线
      const resUserTimes = this.$http.post("getUserTings");
      resUserTimes.then(r => {
        this.userTimes = r.data.data;
      });
    },
    deleteTimes(data) {
      //点击删除
      if (confirm("确定要删除" + data.time + "事件")) {
        //点击确定
        const deletTimes = this.$http.post("setUserTings", {
          text: data.text
        });
        const loading = this.$animationLoad();
        deletTimes.then(r => {
          loading.close();
          this.$message.success(r.data.msg);
          this.$router.go(0);
        });
      }
    },
    CloseAddLog() {
      //关闭添加事情线
      this.chTime = "";
      this.dateText = "";
    },
    onAddLog(is) {
      //点击添加取消/确定
      if (is == 0) {
        //取消
        this.isAddLog = false;
      } else {
        //确定
        if (this.dateText != "" && this.chTime != "") {
          const addTimes = this.$http.post("addUserTings", {
            text: this.dateText,
            time: this.chTime,
            user_id: "0"
          });
          const loading = this.$animationLoad();
          addTimes.then(r => {
            loading.close();
            this.$message.success(r.data.msg);
            this.isAddLog = false;
            this.$router.go(0);
          });
        } else {
          this.$message.warning("请填充完整!");
        }
      }
    }
  }
};
</script>
<style scoped lang="less">
#addTimes {
  margin-left: 10px;
}
.el-textarea {
  margin-top: 10px;
}
</style>