<template>
  <div class="container marketing">
    <!-- 表格 -->
    <el-table :data="userData" style="width: 100%" stripe height="580">
      <el-table-column prop="type" label="类型" width="180"></el-table-column>
      <el-table-column prop="value" label="值" width="400"></el-table-column>
      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showUpdateLog(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 修改对话框 -->
    <el-dialog
      id="updateLog"
      title="提示"
      :visible.sync="isUpdateLog"
      width="80%"
      :before-close="CloseUpdateLog"
      :show-close="false"
    >
      <el-input v-model="update_value" placeholder="请输入修改内容"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onUpDataLog(0)">取 消</el-button>
        <el-button type="primary" @click="onUpDataLog(1)">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getData();
  },
  data() {
    return {
      mainUserData: [], //数据
      updateKey: "", //修改的键
      userData: [],
      isUpdateLog: false, //显示修改对话框
      update_value: "" //修改的内容
    };
  },
  methods: {
    showUpdateLog(data) {
      //点击进入修改数据
      this.isUpdateLog = true;
      this.updateKey = data.type;
    },
    onUpDataLog(is) {
      //确定/取消修改数据回调
      if (is == 0) {
        this.isUpdateLog = false;
        this.update_value = "";
      } else {
        switch (this.updateKey) {
          case "用户名":
            this.mainUserData[0].name = this.update_value;
            break;
          case "名句":
            this.mainUserData[0].quotes = this.update_value;
            break;
          case "QQ":
            this.mainUserData[0].qq = this.update_value;
            break;
          case "微信":
            this.mainUserData[0].wx = this.update_value;
            break;
          case "手机":
            this.mainUserData[0].phone = this.update_value;
            break;
          case "邮箱":
            this.mainUserData[0].email = this.update_value;
            break;
          case "进入码:喜欢":
            this.mainUserData[0].loveText = this.$getCodePwd(this.update_value);
            break;
          case "进入码:后台":
            this.mainUserData[0].serviceText = this.$getCodePwd(
              this.update_value
            );
            break;
          case "备注名":
            this.mainUserData[0].note = this.update_value;
            break;
        }
        //已经修改后的数据
        const resData = this.$http.post("setUserData", this.mainUserData[0]);
        const loading = this.$animationLoad();
        resData.then(r => {
          loading.close();
          this.$message.success("上传成功!");
          this.isUpdateLog = false;
          this.$router.go(0);
        });
      }
    },
    CloseUpdateLog() {
      //关闭修改数据回调
      this.update_value = "";
    },
    getData() {
      //获取数据
      const resUserData = this.$http.post("getUserData");
      resUserData.then(r => {
        const data = r.data.data;
        this.mainUserData = data;
        this.userData.push({ type: "用户名", value: data[0].name });
        this.userData.push({ type: "名句", value: data[0].quotes });
        this.userData.push({ type: "QQ", value: data[0].qq });
        this.userData.push({ type: "微信", value: data[0].wx });
        this.userData.push({ type: "手机", value: data[0].phone });
        this.userData.push({ type: "邮箱", value: data[0].email });
        this.userData.push({
          type: "进入码:喜欢",
          value: this.$setCodePwd(data[0].loveText)
        });
        this.userData.push({
          type: "进入码:后台",
          value: this.$setCodePwd(data[0].serviceText)
        });
        this.userData.push({ type: "备注名", value: data[0].note });
      });
    }
  }
};
</script>
