<template>
  <div class="container marketing">
    <!-- Three columns of text below the carousel -->
    <div class="row">
      <!-- apk区 -->
      <div class="col-lg-3 WorksList">
        <img
          width="30"
          height="30"
          class="img-circle"
          alt="Generic placeholder image"
          src="../assets/image/works_apk.png"
        />
        <el-card class="box-card row apk_List">
          <!-- apk子文件 -->
          <div
            class="apkItem"
            @mousemove="inApkItemCss(i)"
            @mouseout="outApkItemCss(i)"
            v-for="(item,i) in apklist"
            :key="i"
          >
            <a :href="$Http.httpApkFIle+item+'.apk'" :download="item+'.apk'">
              <img class="apkDownload" src="../assets/image/download.png" />
            </a>
            <img :src="$Http.httpApkImage+item+'.png'" />
            <br />
            <span>{{formText(item,8)}}</span>
          </div>
        </el-card>
      </div>
      <!-- html区 -->
      <div class="col-lg-3 WorksList">
        <img
          width="30"
          height="30"
          class="img-circle"
          alt="Generic placeholder image"
          src="../assets/image/works_html.png"
        />
        <el-card class="box-card row html_List">
          <div class="htmlItem" v-for="(item,i) in htmllist" :key="i">
            <a
              :href="$Http.httpHtmlFIle+(item.name)+'.rar'"
              :download="item+'.rar'"
              target="_blank"
            >
              <el-button type="success" icon="el-icon-download" circle size="mini"></el-button>
            </a>
            <span>{{item.name}}</span>
            <br />
            <a :href="item.path" target="_blank">
              <img :src="$Http.httpHtmlImage+item.name+'.png'" />
            </a>
          </div>
        </el-card>
      </div>
      <!-- word区 -->
      <div class="col-lg-3 WorksList">
        <img
          width="30"
          height="30"
          class="img-circle"
          alt="Generic placeholder image"
          src="../assets/image/works_word.png"
        />
        <el-card class="box-card word_List">
          <div class="wordItem" v-for="(item,i) in wordlist" :key="i">
            <el-card class="box-card spenLeft">
              <div slot="header" class="clearfix">
                <span id="word_text_time">{{item.time}}</span>
                <a :href="$Http.httpWordFIle +(item.name)" :download="item.name">
                  <el-button style="float: right; padding: 3px 0;" type="text">download</el-button>
                </a>
              </div>
              {{item.name}}
            </el-card>
          </div>
        </el-card>
      </div>
      <!-- /.col-lg-4 -->
      <div class="col-lg-3 WorksList">
        <img
          @click="getData"
          width="30"
          height="30"
          class="img-circle"
          alt="Generic placeholder image"
          src="../assets/image/works_etc.png"
        />
        <el-card class="box-card etc_List">
          <i class="el-icon-loading"></i>
          <span>
            敬请期待......
            <br />Stay tuned for...
          </span>
        </el-card>
      </div>
      <!-- /.col-lg-4 -->
    </div>
    <!-- /.row -->
  </div>
</template>
<script>
export default {
  created() {
    this.getData();
    //托管方法
  },
  data() {
    return { apklist: [], htmllist: [], wordlist: [] };
  },
  methods: {
    inApkItemCss(index) {
      //修改为下载
      console.log(index);
      $($(".apkDownload")[index]).css({ opacity: "8" }, 10);
    },
    outApkItemCss(index) {
      $($(".apkDownload")[index]).css({ opacity: "0" }, 10);
    },
    onApkIntem(index) {
      alert(index);
    },
    //获取数据
    getData() {
      const resApk = this.$http.post("getUserApk");
      resApk.then(r => {
        this.apklist = r.data.data;
      });
      const resHtml = this.$http.post("getUserHtml");
      resHtml.then(r => {
        let data = r.data.data;
        for (let i = 0; i < data.length; i++) {
          let dataClass = data[i].split("$:");
          this.htmllist.push({
            name: this.formText(dataClass[0], 12),
            path: dataClass[1]
          });
        }
      });
      const resWord = this.$http.post("getUserWord");
      resWord.then(r => {
        let data = r.data.data;
        for (let i = 0; i < data.length; i++) {
          let dataClass = data[i].split("$:");
          this.wordlist.push({
            name: this.formText(dataClass[0], 25),
            time: dataClass[1]
          });
        }
      });
    },
    formText(s, len) {
      //格式化文本不超出
      if (s.length > len) {
        return (
          s.substring(0, len) + "***" + s.substring(s.length - 2, s.length)
        );
      } else {
        return s;
      }
    }
  }
};
</script>
<style scoped lang="less">
#main_content {
  margin-top: 10%;
}

@media (max-width: 767px) {
  #main_content {
    margin-top: 23%;
  }
}
.apkItem {
  width: 45px;
  height: 70px;
  float: left;
  margin: 0 auto;
  padding: 2%;
  &:hover {
    cursor: pointer;
  }
  img {
    width: 100%;
    height: 70%;
  }
  span {
    font-size: 10px;
    overflow: hidden;
    display: block; /*内联对象需加*/
    word-break: keep-all; /* 不换行 */
  }
  .apkDownload {
    position: absolute;
    width: 40px;
    height: 40px;
    margin-top: 5px;
    opacity: 0;
  }
}
.WorksList {
  height: 400px;
  .el-card {
    background: rgba(255, 255, 255, 0.158);
    border: 0;
    border-radius: 10px;
  }
  .img-circle {
    margin-bottom: 1%;
  }
  .apk_List {
    height: 90%;
    width: 80%;
    margin: 0 auto;
    background: url("../assets/image/works_apkBg.png");
    background-size: 125%;
    background-repeat: no-repeat;
    border: 10px solid rgb(54, 54, 54);
    border-radius: 20px;
    overflow: auto;
  }
  .html_List {
    height: 90%;
    margin: 0 auto;
    overflow: auto;
    .htmlItem {
      width: 100%;
      text-align: center;
      font-size: 15px;
      .el-button {
        margin-bottom: 1%;
        margin-right: 10%;
      }
      span {
        font-size: 12px;
        color: white;
        margin-bottom: 1%;
      }
      img {
        width: 100%;
        height: 30%;
        border-radius: 10px;
        margin-bottom: 10px;
        &:hover {
          transition: 0.4s;
          cursor: pointer;
          box-shadow: 5px 10px 10px silver;
        }
      }
    }
  }
  .word_List {
    height: 90%;
    margin: 0 auto;
    overflow: auto;
    .wordItem {
      margin-bottom: 15px;
    }
  }

  /deep/ .el-card__header {
    padding: 5px;
  }
  .spenLeft {
    text-align: left;
    font-size: 12px;
    color: white;
    div,
    .el-button {
      color: rgb(103, 194, 58);
      margin-right: 2px;
    }
    #word_text_time {
      color: white;
      margin-left: 10px;
    }
  }
}
.etc_List {
  text-align: center;
  width: 100%;
  height: 90%;
  color: white;
  line-height: 40px;
}
</style>