<template>
  <div>
    <el-table :data="userOpinions" style="width: 100%" stripe height="520">
      <el-table-column type="index" label="序号" width="100"></el-table-column>
      <el-table-column prop="opinion_dome" label="意见头" width="300"></el-table-column>
      <el-table-column prop="opinionText" label="内容" width="400"></el-table-column>
      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteTimes(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  created() {
    this.getDate();
  },
  data() {
    return { userOpinions: [] };
  },
  methods: {
    getDate() {
      const resUserOpinion = this.$http.post("getUserOpinion");
      resUserOpinion.then(r => {
        let opinions = r.data.data;
        opinions.sort(function(a, b) {
          return b.id - a.id;
        });
        for (let i = 0; i < opinions.length; i++) {
          this.userOpinions.push({
            opinion_dome: opinions[i].opinion_dome + ":" + opinions[i].id,
            opinionText: opinions[i].opinionText,
            id: opinions[i].id
          });
        }
      });
    },
    deleteTimes(data) {
      //删除opinion
      if (confirm("确定要删除" + data.opinion_dome + "的意见")) {
        const deleteOpinion = this.$http.post("deleteUserOpinion", {
          opinionId: data.id
        });
        const loading = this.$animationLoad();
        deleteOpinion.then(r => {
          loading.close();
          this.$message.success(r.data.msg);
          this.$router.go(0);
        });
      }
    }
  }
};
</script>
