<template>
  <div class="container marketing">
    <el-page-header @back="goBack" content="后台管理"></el-page-header>
    <!-- 路由占位符 -->
    <router-view></router-view>
    <!-- 打开侧拉 -->
    <img src="@/assets/image/serviceHome_opMenu.png" id="opMenu" @click="openMenu" />
    <!-- 侧拉：
    direction：方向，ltr左--右
    :visible.sync:是否显示
    size:宽度
    -->
    <el-drawer
      id="rightDrawer"
      title="后台系统"
      :visible.sync="isDrawer"
      direction="ltr"
      size="42%"
      :show-close="false"
      :before-close="closeMenu"
    >
      <!-- 导航
     default-active:默认选择
     class：菜单类型
     
      -->
      <el-menu
        :default-active="brMenu"
        class="el-menu-vertical-demo"
        background-color="white"
        text-color="#000"
        active-text-color="#ffd04b"
      >
        <el-menu-item
          :index="item.name"
          v-for="(item,i) in menus"
          :key="i"
          @click="toServicePage(i)"
        >
          <i :class="item.icon"></i>
          <el-badge v-show="i==menus.length-1" :value="opinionLen">我的意见</el-badge>
          <span slot="title" v-show="i!=menus.length-1">{{item.name}}</span>
        </el-menu-item>
      </el-menu>
    </el-drawer>
  </div>
</template>
<script>
export default {
  created() {
    this.initView();
  },
  data() {
    return {
      isDrawer: false,
      menus: [
        { name: "我的", path: "/serviceMy", icon: "el-icon-user" },
        { name: "事情线", path: "/serviceTimes", icon: "el-icon-s-help" },
        {
          name: "图片管理",
          path: "/serviceImages",
          icon: "el-icon-picture-outline"
        },
        {
          name: "apk数据管理",
          path: "/serviceApk",
          icon: "el-icon-mobile-phone"
        },
        {
          name: "html数据管理",
          path: "/serviceHtml",
          icon: "el-icon-bank-card"
        },
        {
          name: "word数据管理",
          path: "/serviceWord",
          icon: "el-icon-notebook-2"
        },
        {
          name: "我的意见",
          path: "/serviceOpinion",
          icon: "el-icon-warning-outline"
        }
      ],
      brMenu: "我的",
      opinionLen: 0
    };
  },
  methods: {
    //初始化页面
    initView() {
      //获取高亮
      this.brMenu = window.sessionStorage.getItem("brMenu");
      if (this.brMenu == null || this.brMenu == "") {
        this.brMenu = this.menus[0].name;
      }
      //显示意见条数
      const resUserOpinion = this.$http.post("getUserOpinion");
      resUserOpinion.then(r => {
        this.opinionLen = r.data.data.length;
      });
    },
    //跳转页面
    toServicePage(i) {
      this.$router.replace(this.menus[i].path);
      this.isDrawer = false;
      window.sessionStorage.setItem("brMenu", this.menus[i].name);
    },
    goBack() {
      //返回为主页
      this.$router.replace("/home");
    },
    openMenu() {
      //打开菜单
      this.isDrawer = true;
      //旋转动画
      $("#opMenu").css({ transform: "rotate(180deg)" });
    },
    closeMenu() {
      //关闭菜单
      this.isDrawer = false;
      //旋转动画
      $("#opMenu").css({ transform: "rotate(0)" });
    }
  }
};
</script>
<style scoped lang="less">
#opMenu {
  position: absolute;
  top: 80%;
  left: 80%;
  width: 50px;
  height: 50px;
  transition: 0.8s;
  z-index: 100;

  &:hover {
    cursor: pointer;
    width: 45px;
    height: 45px;
    transition: 0.4s;
  }
}
.el-page-header {
  margin: 5%;
}
.el-menu-item {
  text-align: left;
}
</style>