<template>
  <div class="container">
    <div class="row">
      <!-- 主页面左主布局 -->
      <div class="col-lg-5">
        <div id="home_left">
          <span id="home_left_text">
            <span id="home_text_note">{{ userData[0].note }}</span>
            <br />
            <span id="home_text_name" style="  font-size: 15px;">{{ userData[0].name }}</span>
            <br />
            <span id="home_text_quotes">{{ userData[0].quotes }}</span>
          </span>
          <el-carousel>
            <el-carousel-item v-for="item in imagsHome" :key="item">
              <el-image :src="item"></el-image>
            </el-carousel-item>
          </el-carousel>
        </div>
      </div>
      <!-- 主页面右布局 -->
      <div class="col-lg-6">
        <div class="row">
          <div class="col-lg-10">
            <img src="../assets/image/home_title.png" height="120px" width="120px" />
          </div>
        </div>
        <div class="row">
          <div id="home_reight_timeline">
            <el-timeline>
              <el-timeline-item
                v-for="(timeIntem, index) in userTimes"
                :key="index"
                :icon="timeIntem.icon"
                :type="timeIntem.type"
                :color="timeIntem.color"
                :size="timeIntem.size"
                :timestamp="timeIntem.timestamp"
              >
                <span id="timeline_text">{{timeIntem.content}}</span>
              </el-timeline-item>
            </el-timeline>
          </div>
        </div>
        <!-- 形象区 -->
        <div class="row">
          <div id="home_reight_userImgs">
            <el-card class="box-card">
              {{textLanguage.textImage}}:
              <br />
              <br />
              <div class="row" id="home_images">
                <div class="col-lg-3">
                  <el-image :src="imagsUser[0]" @click="SelectUsers(0)"></el-image>
                </div>
                <div class="col-lg-3">
                  <el-image :src="imagsUser[1]" @click="SelectUsers(1)"></el-image>
                </div>
                <div class="col-lg-3">
                  <el-image :src="imagsUser[2]" @click="SelectUsers(2)"></el-image>
                </div>
                <div class="col-lg-3">
                  <el-image :src="imagsUser[3]" @click="SelectUsers(3)"></el-image>
                </div>
              </div>
            </el-card>
          </div>
        </div>
      </div>
      <!-- 底 -->
    </div>
    <el-dialog :visible.sync="isSelectImage" @close="closeDialogImage" title="预览图像">
      <el-carousel indicator-position="outside" height="350px">
        <el-carousel-item v-for="(src) in imagesUserSelect" :key="src">
          <img :src="src" style="max-width: 100%;max-height: 100%;display: block; margin: 0 auto;" />
        </el-carousel-item>
      </el-carousel>
    </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  created() {
    //刚进入页面获取数据
    this.getData();
  },
  components: {},
  data() {
    return {
      isSelectImage: false,
      imagsUser: [], //用户图像
      imagesUserSelect: [], //预览图像
      imagsHome: [], //主页图片
      userData: [{ name: "加载中", quotes: "加载中", note: "加载中" }], //用户信息
      userTimes: [], // 时间线
      textLanguage: { textImage: "形象墙" } //显示英文
    };
  },
  methods: {
    // 关闭预览图片
    closeDialogImage() {
      this.isSelectImage = false;
      this.imagesUserSelect.splice(0, this.imagesUserSelect.length);
    },
    getData() {
      //修改语言
      this.updateHomeText();
      //获取所有图像
      const resUserImg = this.$http.post("getUserImg");
      resUserImg.then(r => {
        const images = r.data.data;
        //分类存放
        for (let i = 0; i < images.length; i++) {
          if (images[i].indexOf("home") != -1) {
            this.imagsHome.push(this.$Http.httpImage + images[i]);
          } else {
            this.imagsUser.push(this.$Http.httpImage + images[i]);
          }
        }
      });
      //获取基本信息
      const resUserData = this.$http.post("getUserData");
      resUserData.then(r => {
        this.userData = r.data.data;
      });
      //获取时间线
      const resUserTimes = this.$http.post("getUserTings");
      resUserTimes.then(r => {
        const times = r.data.data;
        for (let i = 0; i < times.length; i++) {
          this.userTimes.push({
            content: times[i].text,
            timestamp: times[i].time,
            size: "large",
            type: "primary",
            icon: "el-icon-loading"
          });
        }
        this.userTimes.push({
          content: "只要学不死，就往死里学。",
          timestamp: "以后",
          size: "large",
          color: "rgb(192, 65, 76)",
          icon: "el-icon-loading"
        });
      });
    },
    SelectUsers(index) {
      for (let i = 0; i < this.imagsUser.length; i++) {
        if (i == index) {
          this.imagesUserSelect.push(this.imagsUser[i]);
          break;
        }
      }
      for (let i = 0; i < this.imagsUser.length; i++) {
        if (i != index) {
          this.imagesUserSelect.push(this.imagsUser[i]);
        }
      }
      this.isSelectImage = true;
    },
    updateHomeText() {
      if (!this.$Http.isZh) {
        this.textLanguage.textImage = "Image of the wall";
      }
    }
  }
};
</script>
