<template>
  <div>
    <div class="row">
      <div class="col-lg-1">
        <el-button type="success" @click="isAddLog = true">添加</el-button>
      </div>
    </div>
    <el-table :data="userWords" style="width: 100%" stripe height="520">
      <el-table-column prop="name" label="文件名" width="300"></el-table-column>
      <el-table-column prop="time" label="时间" width="400"></el-table-column>
      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteTimes(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加对话框 -->
    <el-dialog
      title="添加Word"
      :visible.sync="isAddLog"
      width="80%"
      :before-close="CloseAddLog"
      :show-close="false"
    >
      <form enctype="multipart/form-data" id="formAdd">
        <div class="row">
          <div class="col-lg-12">
            <el-input type="text" placeholder="文件名(一定带上后缀)" name="fileName" v-model="wordName" />
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <!-- 选择时间:
    type:类型
    editable：是否可以输入
    format：显示格式
    value-format:输出格式
            -->
            <el-date-picker
              id="dateAddLog"
              v-model="wordTime"
              type="datetime"
              placeholder="选择日期时间"
              :editable="false"
              format="yyyy 年 MM 月 dd 日"
              value-format="yyyy年MM月dd"
              name="fileTime"
            ></el-date-picker>
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <el-tag type="success">word文件:</el-tag>
            <input type="file" id="file_file" name="fileWord" />
          </div>
        </div>
      </form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onAddLog(0)">取 消</el-button>
        <el-button type="primary" @click="onAddLog(1)">
          上传
          <i class="el-icon-upload el-icon--right"></i>
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getDate();
  },
  data() {
    return { isAddLog: false, userWords: [], wordTime: "", wordName: "" };
  },
  methods: {
    getDate() {
      const resUserWord = this.$http.post("getUserWord");
      resUserWord.then(r => {
        const words = r.data.data;
        for (let i = 0; i < words.length; i++) {
          let dataClass = words[i].split("$:");
          this.userWords.push({
            name: dataClass[0],
            time: dataClass[1]
          });
        }
      });
    },
    CloseAddLog() {
      //关闭添加对话框
      $("#file_file").val("");
      this.wordTime = "";
      this.wordName = "";
    },
    onAddLog(is) {
      //点击添加取消/确定
      if (is == 0) {
        //取消
        this.isAddLog = false;
      } else {
        //确定
        let name = this.wordName;
        let time = this.wordTime;
        let file = $("#file_file");
        if (name == "" || time == "" || file.val() == "") {
          this.$message.warning("请填充完整!");
        } else {
          var formData = new FormData(document.getElementById("formAdd")); //获取form里的数据
          const addApk = this.$http.post("addUserWord", formData);
          const loading = this.$animationLoad();
          addApk.then(r => {
            loading.close();
            this.isAddLog = false;
            this.$message.success(r.data.msg);
            this.$router.go(0);
          });
        }
      }
    },
    deleteTimes(data) {
      //删除apk文件
      if (confirm("确定要删除" + data.name + "文件")) {
        //点击确定
        const deletImage = this.$http.post("setUserWord", {
          wordName: data.name
        });
        const loading = this.$animationLoad();
        deletImage.then(r => {
          loading.close();
          this.$message.success(r.data.msg);
          this.$router.go(0);
        });
      }
    }
  }
};
</script>
<style scoped lang = 'less'>
#formAdd {
  div {
    margin-top: 10px;
    .col-lg-12 .el-tag {
      margin-right: 10px;
    }
  }
}
</style>