<template>
  <div class="container marketing">
    <!-- Three columns of text below the carousel -->
    <div class="row">
      <!-- 联系区 -->
      <div class="col-lg-3 contact_con">
        <el-card>
          <div class="contact_con">
            <img src="@/assets/image/contact_qq.png" />
            <span>:{{userData[0].qq}}</span>
          </div>
          <div class="contact_con">
            <img src="@/assets/image/contact_wx.png" />
            <span>:{{userData[0].wx}}</span>
          </div>
          <div class="contact_con">
            <img src="@/assets/image/contact_phone.png" />
            <span>:{{userData[0].phone}}</span>
          </div>
          <div class="contact_con">
            <img src="@/assets/image/contact_yx.png" />
            <span>:{{userData[0].email}}</span>
          </div>
        </el-card>
      </div>
      <!-- 意见区 -->
      <div class="col-lg-6 contact_opinion">
        <img src="@/assets/image/contact_opinion.png" />
        <el-card>
          <div id="opinion_dome">
            <span>{{textLanguage.opinion_domes1}}：</span>
            <br />
            <el-select v-model="opinion_domes_value" :placeholder="textLanguage.opinion_domes2">
              <el-option
                v-for="item in opinion_domes"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <div id="opinion_text">
            <span>{{textLanguage.opinion_text1}}：</span>
            <br />
            <form role="form">
              <div class="form-group">
                <textarea
                  @input="OpinionUPdateText()"
                  class="form-control"
                  rows="10"
                  :placeholder="textLanguage.opinion_text2"
                  v-model="opinion_text_value"
                ></textarea>
              </div>
            </form>
          </div>
          <el-button @click="OpinionUpLoad" type="success" icon="el-icon-check" circle></el-button>
        </el-card>
      </div>
    </div>
    <!-- /.row -->
  </div>
</template>
<script>
export default {
  created() {
    this.getData();
  },
  data() {
    return {
      opinion_text: "",
      opinion_domes_value: "",
      opinion_text_value: "",
      userData: [
        { qq: "正在加载", wx: "正在加载", phone: "正在加载", email: "正在加载" }
      ],
      opinion_domes: [{ value: "", label: "" }],
      textLanguage: {
        opinion_domes1: "项目",
        opinion_domes2: "默认(其他)",
        opinion_text1: "内容",
        opinion_text2: "请输入你的意见"
      }, //显示英文
      isOpinionUpload: false
    };
  },
  methods: {
    OpinionUpLoad() {
      //加载动画;
      const loading = this.$animationLoad();
      //点击上传意见
      let op_dome = "其他";
      let op_text = "";
      if (this.opinion_domes_value != "") {
        //没有选择默认其他
        op_dome = this.opinion_domes[this.opinion_domes_value].label;
      }
      op_text = this.opinion_text_value;
      const resOpinion = this.$http.post("addUserOpinion", {
        opinionDome: op_dome,
        opinionText: op_text
      });
      resOpinion.then(r => {
        //成功关闭动画
        if (r.data.state == 1) {
          loading.close();
          this.$message.success("上传成功!");
          this.opinion_text_value = "";
          this.opinion_domes_value = "";
        } else {
          loading.close();
          this.$message.error("上传失败!");
        }
      });
    },
    getData() {
      //修改语言
      if (!this.$Http.isZh) {
        this.textLanguage = {
          opinion_domes1: "project",
          opinion_domes2: "The default (other)",
          opinion_text1: "content",
          opinion_text2: "Please input your opinion"
        }; //显示英文
      }
      //获取数据
      const resData = this.$http.post("getUserData");
      resData.then(r => {
        this.userData = r.data.data;
      });
      //获取所有项目内容:链式调用
      const resApk = this.$http.post("getUserApk");
      const resHtml = this.$http.post("getUserHtml");
      const resWord = this.$http.post("getUserWord");
      let index = 0;
      resApk.then(r => {
        let data = r.data.data;
        for (let i = 0; i < data.length; i++, index++) {
          let dataClass = data[i].split("$:");
          this.opinion_domes.push({
            value: index,
            label: dataClass[0] + ":apk"
          });
        }
        resHtml.then(r => {
          let data = r.data.data;
          for (let i = 0; i < data.length; i++, index++) {
            let dataClass = data[i].split("$:");
            this.opinion_domes.push({
              value: index,
              label: dataClass[0] + ":html"
            });
          }
          resWord.then(r => {
            let data = r.data.data;
            for (let i = 0; i < data.length; i++, index++) {
              let dataClass = data[i].split("$:");
              this.opinion_domes.push({
                value: index,
                label: dataClass[0] + +":word"
              });
            }
          });
        });
      });
    },
    OpinionUPdateText() {
      //判断动画
      if (this.opinion_text_value != "") {
        $(" .contact_opinion  .el-card").css({ height: "430px" });
        $(" .contact_opinion  .el-button").animate({ opacity: "1" }, 100);
      } else {
        $(" .contact_opinion  .el-card").css({ height: "400px" });
        $(" .contact_opinion  .el-button").animate({ opacity: "0" }, 100);
      }
    }
  }
};
</script>
<style scoped lang="less">
#main_content {
  margin-top: 9%;
}

@media (max-width: 767px) {
  #main_content {
    margin-top: 23%;
  }
}
.contact_con {
  margin-bottom: 10px;
  .el-card {
    margin-top: 30%;
    height: auto;
    background: rgba(255, 255, 255, 0.158);
    border: 0;
    color: white;
    text-align: left;
    .contact_con {
      margin: 6%;
    }
    img {
      width: 30px;
      height: 30px;
      margin-right: 5%;
    }
  }
}
.contact_opinion {
  margin-left: 20%;
  .el-card {
    height: 400px;
    background: rgba(255, 255, 255, 0.158);
    color: white;
    text-align: left;
    border: 0;
    border-radius: 10px;
  }
  img {
    width: 30px;
    height: 30px;
    margin-right: 5%;
    margin-bottom: 2%;
  }
  .el-button {
    margin-left: 90%;
    opacity: 0;
  }
  @media (max-width: 767px) {
    .el-button {
      margin-left: 80%;
    }
  }
}
@media (max-width: 767px) {
  .contact_opinion {
    margin-left: 0%;
  }
}
#opinion_dome {
  .el-select {
    width: 100%;
  }
}
</style>