<template>
  <el-container id="main_container">
    <b-navbar toggleable="lg" type="dark" id="nav">
      <b-container>
        <!-- 左边,主页标志 -->
        <b-navbar-brand href="#"><img src=../assets/image/logo.png id="main_logo"></b-navbar-brand>
        <!-- 小话后按键的样式，默认是三横 -->
        <b-navbar-toggle target="nav-collapse" @click="onNavUpateCss"></b-navbar-toggle>
        <!-- 其他部分 -->
        <b-collapse id="nav-collapse" is-nav>
          <!-- 左边部分中的中间 -->
          <b-navbar-nav class="ml-auto container">
            <div class="container" id="nav_list">
              <el-tabs v-model="chNav" @tab-click="clickNav">
                <el-tab-pane :label="homeTitle.home" name="home"></el-tab-pane>
                <el-tab-pane :label="homeTitle.works" name="works"></el-tab-pane>
                <el-tab-pane :label="homeTitle.contact" name="contact"></el-tab-pane>
              </el-tabs>
            </div>
          </b-navbar-nav>
          <!-- 左边部分中的右边 -->
          <b-navbar-nav class="ml-auto">
            <b-nav-item-dropdown right>
              <!-- Using 'button-content' slot -->
              <template slot="button-content">
                <i class="el-icon-s-tools" style="color:  rgb(192, 80, 80);font-size:30px"></i>
              </template>

              <b-dropdown-item @click="toServiceHome" class="nav_main_dropdown">
                <i class="el-icon-edit"></i>
                <el-badge :value="opinionLen" v-if="opinionLen!=0">后台服务</el-badge>
                <span v-if="opinionLen==0">后台服务</span>
              </b-dropdown-item>
              <b-dropdown-item href="#" class="nav_main_dropdown">
                <i class="el-icon-view"></i>
                <el-badge>喜欢</el-badge>
              </b-dropdown-item>
            </b-nav-item-dropdown>
          </b-navbar-nav>
        </b-collapse>
      </b-container>
    </b-navbar>
    <el-main>
      <!-- 路由占位符 -->
      <router-view id="main_content"></router-view>
    </el-main>
    <!-- 弹出窗口 
    show-close：是否显示关闭按钮
    fullscreen是否全屏
    close-on-click-modal:点击判别是否关闭
    close-on-press-escape：点击esc是否关闭
    -->
    <el-dialog
      id="initHomeLog"
      :visible.sync="isHomeLog"
      width="30%"
      :show-close="false"
      fullscreen
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <div class="spinner">
        <div class="cube1"></div>
        <div class="cube2"></div>
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
export default {
  created() {
    this.getData();
    this.initHomeTitle();
  },
  data() {
    return {
      isHomeLog: true,
      chNav: "home",
      bgImage: "",
      userData: [],
      homeTitle: { home: "首页", works: "作品", contact: "联系" },
      opinionLen: "0" //意见的消息
    };
  },
  methods: {
    // 页面加载完毕初始化
    initView() {
      setTimeout(() => {
        this.isHomeLog = false;
      }, 1000);
    },
    // 主页面
    onNavUpateCss() {
      const classv = $("#nav-collapse").attr("class");
      if (classv.indexOf("show") == -1) {
        $("#nav").animate({ height: "25%" });
      } else {
        $("#nav").animate({ height: "12%" });
      }
    },
    toServiceHome() {
      var text = prompt("请输入您的进入码:", "进入码");
      if (text != null && text != "") {
        //判断是否正确并存入sessionStorage
        if (this.$setCodePwd(this.userData[0].serviceText) == text) {
          this.$message.success("你好！我的主人。");
          window.sessionStorage.setItem(
            "isService",
            this.userData[0].serviceText
          );
          this.$router.replace("/serviceHome");
        } else {
          this.$message.error("输入错误!");
        }
      }
    },
    clickNav() {
      // this.$message.error(this.chNav);
      this.$router.replace("/" + this.chNav);
    },
    getData() {
      //获取所有图像
      const resUserImg = this.$http.post("getUserImg");
      resUserImg.then(r => {
        const images = r.data.data;
        //分类存放
        for (let i = 0; i < images.length; i++) {
          if (images[i].indexOf("mainBg") != -1) {
            this.bgImage = this.$Http.httpImage + images[i];
            break;
          }
        }
        if (this.bgImage != "")
          $(".el-main").css({
            background: "url(" + this.bgImage + ")",
            backgroundSize: "cover"
          });
      });
      //获取基本数据
      //获取基本信息
      const resData = this.$http.post("getUserData");
      resData.then(r => {
        //进入网络就关闭加载动画
        this.initView();
        this.userData = r.data.data;
      });
    },
    initHomeTitle() {
      //中英文转
      if (navigator.language != "zh-CN") {
        this.homeTitle.home = "home";
        this.homeTitle.works = "works";
        this.homeTitle.contact = "contact";
        this.$Http.isZh = false;
      }
      //获取意见消息条数
      //显示意见条数
      const resUserOpinion = this.$http.post("getUserOpinion");
      resUserOpinion.then(r => {
        this.opinionLen = r.data.data.length;
      });
    }
  }
};
</script>

