<template>
  <div>
    <div class="row">
      <div class="col-lg-1">
        <el-button type="success" @click="isAddLog = true">添加</el-button>
      </div>
    </div>
    <el-table :data="userHtmls" style="width: 100%" stripe height="520">
      <el-table-column prop="name" label="html名" width="300"></el-table-column>
      <el-table-column prop="path" label="修改链接" width="400"></el-table-column>
      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteTimes(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加对话框 -->
    <el-dialog
      title="添加apk"
      :visible.sync="isAddLog"
      width="80%"
      :before-close="CloseAddLog"
      :show-close="false"
    >
      <form enctype="multipart/form-data" id="formAdd">
        <div class="row">
          <div class="col-lg-12">
            <el-input type="text" placeholder="文件名(可以不带后缀)" name="fileName" v-model="htmlName" />
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <el-input type="text" placeholder="相关链接" name="fileHref" v-model="htmlPath" />
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <el-tag type="success">html文件:</el-tag>
            <input type="file" id="file_file" name="fileHtml" />
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <el-tag type="success">html图片:</el-tag>
            <input type="file" id="file_image" name="fileImage" />
          </div>
        </div>
      </form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onAddLog(0)">取 消</el-button>
        <el-button type="primary" @click="onAddLog(1)">
          上传
          <i class="el-icon-upload el-icon--right"></i>
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getDate();
  },
  data() {
    return { isAddLog: false, userHtmls: [], htmlName: "", htmlPath: "" };
  },
  methods: {
    getDate() {
      const resUserhtml = this.$http.post("getUserHtml");
      resUserhtml.then(r => {
        const htmls = r.data.data;
        for (let i = 0; i < htmls.length; i++) {
          let dataClass = htmls[i].split("$:");
          this.userHtmls.push({
            name: dataClass[0],
            path: dataClass[1]
          });
        }
      });
    },
    CloseAddLog() {
      //关闭添加
      $("#file_file").val("");
      $("#file_image").val("");
      this.htmlName = "";
      this.htmlPath = "";
    },
    onAddLog(is) {
      //确定/取消
      if (is == 0) {
        this.isAddLog = false;
      } else {
        //确定
        let name = this.htmlName;
        let href = this.htmlPath;
        let file = $("#file_file");
        let fileImage = $("#file_image");
        if (name == "" || href == "" || fileImage.val() == "") {
          this.$message.warning("请填充完整!");
        } else {
          var formData = new FormData(document.getElementById("formAdd")); //获取form里的数据
          const addApk = this.$http.post("addUserHtml", formData);
          const loading = this.$animationLoad();
          addApk.then(r => {
            loading.close();
            this.isAddLog = false;
            this.$message.success(r.data.msg);
            this.$router.go(0);
          });
        }
      }
    },
    deleteTimes(data) {
      //删除html文件
      if (confirm("确定要删除" + data.name + "文件")) {
        //点击确定
        const deletImage = this.$http.post("setUserHtml", {
          htmlName: data.name
        });
        const loading = this.$animationLoad();
        deletImage.then(r => {
          loading.close();
          this.$message.success(r.data.msg);
          this.$router.go(0);
        });
      }
    }
  }
};
</script>
<style scoped lang = 'less'>
#formAdd {
  div {
    margin-top: 10px;
    .col-lg-12 .el-tag {
      margin-right: 10px;
    }
  }
}
</style>