<template>
  <div>
    <div class="row">
      <div class="col-lg-1">
        <el-button type="success" id="addTimes" @click="isAddLog = true">添加</el-button>
      </div>
    </div>
    <el-table :data="userApks" style="width: 100%" stripe height="520">
      <el-table-column prop="name" label="apk名" width="300"></el-table-column>
      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteTimes(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加对话框 -->
    <el-dialog
      title="添加apk"
      :visible.sync="isAddLog"
      width="80%"
      :before-close="CloseAddLog"
      :show-close="false"
    >
      <form enctype="multipart/form-data" id="formAdd">
        <div class="row">
          <div class="col-lg-12">
            <el-input type="text" placeholder="文件名(可以不带后缀)" name="fileName" v-model="apkName" />
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <el-tag type="success">apk文件:</el-tag>
            <input type="file" id="file_file" name="fileApk" />
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <el-tag type="success">apk图片:</el-tag>
            <input type="file" id="file_image" name="fileImage" />
          </div>
        </div>
      </form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onAddLog(0)">取 消</el-button>
        <el-button type="primary" @click="onAddLog(1)">
          上传
          <i class="el-icon-upload el-icon--right"></i>
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getDate();
  },
  data() {
    return { userApks: [], isAddLog: false, apkName: "" };
  },
  methods: {
    getDate() {
      const resUserApk = this.$http.post("getUserApk");
      resUserApk.then(r => {
        const apks = r.data.data;
        for (let i = 0; i < apks.length; i++) {
          this.userApks.push({
            name: apks[i]
          });
        }
      });
    },
    CloseAddLog() {
      //关闭添加窗口
      this.apkName = "";
      $("#file_file").val("");
      $("#file_image").val("");
    },
    onAddLog(is) {
      if (is == 0) {
        this.isAddLog = false;
      } else {
        //确定/
        let name = this.apkName;
        let file = $("#file_file");
        let fileImage = $("#file_image");
        if (name == "" || file.val() == "" || fileImage.val() == "") {
          this.$message.warning("请填充完整!");
        } else {
          var formData = new FormData(document.getElementById("formAdd")); //获取form里的数据
          const addApk = this.$http.post("addUserApk", formData);
          const loading = this.$animationLoad();
          addApk.then(r => {
            loading.close();
            this.isAddLog = false;
            this.$message.success(r.data.msg);
            this.$router.go(0);
          });
        }
      }
    },
    deleteTimes(data) {
      //删除apk文件
      if (confirm("确定要删除" + data.name + "文件")) {
        //点击确定
        const deletImage = this.$http.post("setUserApk", {
          apkName: data.name
        });
        const loading = this.$animationLoad();
        deletImage.then(r => {
          loading.close();
          this.$message.success(r.data.msg);
          this.$router.go(0);
        });
      }
    }
  }
};
</script>
<style scoped lang="less">
#formAdd {
  div {
    margin-top: 10px;
    .col-lg-12 .el-tag {
      margin-right: 10px;
    }
  }
}
</style>